<template>
	
  <div id="login">
    <div class="outer">
    	<form class="form-signin" method="get" action="${pageContext.request.contextPath}/user/isLoginUser" id="myform">
    		<div class="login-head">
    			<a href="#" class="login-in">登录</a>
    			·
    			<a href="#" class="register">注册</a>
    		</div>
    		<div class="form-item">
    			<label class="sr-only">用户名</label>
    			<input type="text" id="username" name="username" class="form-control" placeholder="用户名" required autofocus>
    		</div>
    		<div class="form-item">
    			<label class="sr-only">密码</label>
    			<input type="password" id="password" name="password" class="form-control" placeholder="密码" required>
    		</div>
    		<div class="checkbox">
    			<input type="checkbox" checked>
    			<span>记住我</span>
    		</div>
    		<div class="problem"><a href="#">登录遇到问题？</a></div>
    		
    		<div class="form-item item-last">
    			<button class="btn btn-lg btn-primary btn-block" type="submit" @click="login()">登录</button>
    		</div>
    	</form>
    </div>
  </div>
</template>
<script src="../js/vue.js" type="text/javascript" charset="utf-8"></script>
<script>
import Vue from 'vue/dist/vue.esm.js'

export default {
	name: 'Login',
	data(){
		  return {}
	},
	mounted() {
		this.loadData();
	},
	beforeDestroy() {
		
	},
	methods:{
		loadData(){
			this.$ajax.post('http://localhost:8080/user/isLoginUser')
			.then(response => {
				console.log(response)
				this.message = response.data
			})
			.catch(error => {
				console.log(error)
			})
		}
	}
  
}
